          v-model="data.cert_number"
        />
      </div>

      <div data-aos="fade" class="flex flex-col">
        <label for="" class="font-semibold text-xs">Nama Lengkap</label>
        <input
          type="text"
          class="border rounded-lg p-2"
          v-model="data.full_name"
        />
      </div>
      <button
        class="bg-purple-500 text-white rounded-md w-full md:w-auto md:rounded-full px-4 py-2"
        @click="verifyData"
      >
